.App {
  text-align: center;
}

.App-header {
  background-color: white;
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding-left: 9%;
  padding-top: 3%;
  padding-right: 9%;
  color: black;
  font-family: Calibri;
}

h1 {
  font-size: 1.2rem;
  text-align: left;
  color: #282928;
}

h2 {
  text-align: left;
  font-size: 2.9rem;
}

p {
  text-align: left;
  font-size: 1 rem;
  color: #282928;
}

/* Media query for mobile devices */
@media only screen and (max-width: 600px) {
  p {
    padding-top: 5%;
    /* Add any other mobile-specific styles here */
  }
}

/* Media query for desktop devices */
@media only screen and (min-width: 601px) {
  p {
    padding-top: 1.2%;
    /* Add any desktop-specific styles here */
  }
}

h2, p {
  margin: 0;
}

.App-link {
  color: #61dafb;
}
