/* Mobile Styles */
@media only screen and (max-width: 600px) {
    /* Resize the image */
    .box1 img {
      width: 100px;
      height: 100px;
    }
  
    /* Adjust font sizes for better readability on small screens */
    .box1 text {
      font-size: 14px;
    }
  
    .box2 text {
      font-size: 14px;
    }
  }
  